import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import {  getUserdata } from "../../../../../components/helpers/function";
class LaporanTimbangBarangExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
    render() {
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Timbang Baki"
          sheet="Laporan Timbang Baki"
          buttonText="Export Exel"
        />
        <table rowkey="kode_member" id="table-to-xls" style={{ display: "none" }}>
        <thead>
              <tr>
                <th colSpan="10" style={{textAlign : "center"}}> LAPORAN TIMBANG BAKI </th>
              </tr>
              <tr>
              <th colSpan="10"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="10"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
            <tr></tr>
            <tr >
                <th  style={{ backgroundColor: "#E8E5E5", color: "#000"}}>NO</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>KODE GUDANG</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>KODE BAKI</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>QTY</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>BERAT</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>BERAT BAKI</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>BERAT BANDROL</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>TOTAL</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>TIMBANGAN</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>SELISIH</th>
                {/* <th style={{ backgroundColor: "#E8E5E5", color: "#000"}}>JUMLAH</th> */}
            </tr>
        </thead>
            
            <tbody>
            {this.props.data.map((item, index) => (
                    <tr key={index}>
                        <td style={{ textAlign : "center" }}> {index+1}</td>
                        <td> {item.kode_gudang}</td>
                        <td> {item.kode_toko}</td>
                        <td> {item.qty}</td>
                        <td> {item.berat}</td>
                        <td> {item.berat_baki}</td>
                        <td> {item.berat_bandrol}</td>
                        <td> {item.total}</td>
                        <td> {item.timbang}</td>
                        <td> {item.selisih}</td>
                        {/* <td style={{ border: "1px solid #000" }}> {item.stock_on_hand}</td> */}
                    </tr>
             ))}
            </tbody>
                    
            <tfoot>
                <tr>
                    <th style={{backgroundColor: "#E8E5E5", color: "#000",textAlign : "center" }} colSpan="3">  </th>    
                    <th style={{backgroundColor: "#E8E5E5", color: "#000",textAlign : "right"}}> {this.props.data.map(list => list.qty).reduce((a,b) => a+b,0)}   </th>    
                    <th style={{backgroundColor: "#E8E5E5", color: "#000",textAlign : "right"}}> {this.props.data.map(list => list.berat).reduce((a,b) => a+b,0)}  </th>    
                    <th style={{backgroundColor: "#E8E5E5", color: "#000",textAlign : "right"}}> {this.props.data.map(list => list.berat_baki).reduce((a,b) => a+b,0)}  </th>    
                    <th style={{backgroundColor: "#E8E5E5", color: "#000",textAlign : "right"}}> {this.props.data.map(list => list.berat_bandrol).reduce((a,b) => a+b,0)}  </th>    
                    <th style={{backgroundColor: "#E8E5E5", color: "#000",textAlign : "right"}}> {this.props.data.map(list => list.total).reduce((a,b) => a+b,0)}  </th>    
                    <th style={{backgroundColor: "#E8E5E5", color: "#000",textAlign : "right"}}> {this.props.data.map(list => list.timbang).reduce((a,b) => a+b,0)}  </th>    
                    <th style={{backgroundColor: "#E8E5E5", color: "#000",textAlign : "right"}}> {this.props.data.map(list => list.selisih).reduce((a,b) => a+b,0)}  </th>    

                    {/* <th style={{backgroundColor: "#E8E5E5", color: "#000"}}> {this.props.data.map(list => list.stock_on_hand).reduce((a,b) => a+b,0)}  </th>     */}
                </tr>
            </tfoot>
        </table>
      </>
    );
  }
}
export default LaporanTimbangBarangExel;
