import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getDataBaki } from "../../../../actions/datamaster_action";
import Tabel from "../../../../components/helpers/tabel";
import Loading from 'react-fullscreen-loading';
import { InputDate } from "../../../../components/helpers/field";
import { ToastNotification } from "../../../../components/helpers/notification";
import { getTglSystem } from "../../../../components/helpers/function";
import LaporanTimbangBarangExel from "./exel/LaporanTimbangBarangExel";
import CetakLaporanTimbangBarang from "./pdf/CetakLaporanTimbangBarang";

class HeadFilterLaporanTimbangBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  componentDidMount() {
    this.props.dispatch(getDataBaki());
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    if(new Date(date) > new Date()){
      ToastNotification(
        "info", "Tanggal tidak boleh lebih dari hari ini"
      )
      return false;
    }
    this.setState({
      tgl_akhir: new Date(date),
    });
  }
  exportPdf() {
    CetakLaporanTimbangBarang(this.props.data);
  }
  render() {
    return (
      <>
        <form
          onSubmit={(data) => this.props.handleSubmit(data)}
          autoComplete="off"
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
        >
          <div className="row">
          <div className="col-lg-4">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

            <div className="col-4 mb-2">
              <label> &nbsp; </label>
              <button
                className="btn btn-primary btn-block"
                disabled={this.submitting}
                type="submit"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Memfilter
                  </>
                ) : (
                  "Lihat Laporan"
                )}
              </button>
            </div>
            <div className="col-12">
              <Tabel
                keyField="kode_barcode"
                data={this.props.data || []}
                columns={this.props.columns}
                empty={true}
                textEmpty="Data Laporan Barang Detail Kosong"
              />
            </div>
            {this.props.export ? (
              <>
                <div className="col-lg-6">
                  <button
                    onClick={() => this.exportPdf()}
                    className="btn btn-warning btn-block"
                  >
                    {" "}
                    Export PDF{" "}
                  </button>
                </div>
                <div className="col-lg-6">
                  <LaporanTimbangBarangExel
                    data={this.props.data || []}
                    kode_baki={this.props.kode_baki}
                  />
                </div>
              </>
            ) : null}
          </div>
          {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
        </form>
      </>
    );
  }
}

HeadFilterLaporanTimbangBarang = reduxForm({
  form: "HeadFilterLaporanTimbangBarang",
  enableReinitialize: true,
})(HeadFilterLaporanTimbangBarang);
export default connect((state) => {
  return {
    databaki: state.datamaster.getDataBaki,
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadFilterLaporanTimbangBarang);
