import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../../components/panel/panel.jsx";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
import { formatDate,formatGram, localStoragedecryp } from "../../../../components/helpers/function.jsx";
import "react-datepicker/dist/react-datepicker.css";
import {
  NotifError,
  ToastNotification,
} from "../../../../components/helpers/notification.jsx";
// import CetakLaporanDetailBarang from "./CetakLaporanDetailBarang.jsx";
// import FormFilter from "./FormFilter.jsx";
import {   getData2, } from "../../../../config/axios.jsx";
import HeadFilterLaporanTimbangBarang from "./HeadFilterLaporanTimbangBarang.jsx";
import moment from "moment";
// import LaporanDetailexel from "./LaporanDetailExel.jsx";
const maptostate = (state) => {
  return {
    hideModal: state.datamaster.modalDialog,
  };
};

class FormLaporanTimbangBarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      export: false,
      kode_baki: [],
      LaporanBarangDetail: false,
      columns: [
        {
          dataField: "kode_gudang",
          text: "Kode Gudang",
        },
        {
          dataField: "kode_toko",
          text: "Kode Baki",
        },

        {
          dataField: "qty",
          text: "Qty",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {formatGram(cell, 3)} </div>;
          },
        },
        {
          dataField: "berat",
          text: "Berat",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {formatGram(cell, 3)} </div>;
          },
        },
        {
          dataField: "berat_baki",
          text: "Berat Baki",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {formatGram(cell, 3)} </div>;
          },
        },
        {
          dataField: "berat_bandrol",
          text: "Berat Bandrol",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {formatGram(cell, 3)} </div>;
          },
        },
        {
          dataField: "total",
          text: "Total",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {formatGram(cell, 3)} </div>;
          },
        },
        {
          dataField: "timbang",
          text: "Timbangan",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {formatGram(cell, 3)} </div>;
          },
        },
        {
          dataField: "selisih",
          text: "Selisih",
          headerClasses: 'text-right',
          formatter: (cell) => {
            return <div className="text-right"> {formatGram(cell, 3)} </div>;
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    let data = JSON.parse(localStoragedecryp("userdata"));
    this.setState({ username: data.user_id });
  }
  handleSubmit(data) {
    let data_post = {
      tgl_awal: formatDate(moment(new Date(data.tgl_awal)).format('YYYY-MM-DD')),
    };
    this.setState({ isLoading: true, kode_toko: data.kode_baki });
    getData2(`timbang-baki/reports?tanggal=${data_post.tgl_awal}`)
      .then((res) => {
        if (res.data.length === 0) {
          this.setState({
            export: false,
            LaporanBarangDetail: [],
          });
          ToastNotification("info", "Laporan Timbang Barang Tidak Tersedia");
        } else {
          ToastNotification("success", "Laporan Timbang Barang Tersedia");
          this.setState({
            export: true,
            LaporanBarangDetail: res.data[0].detail_barang,
          });
          console.log(this.state.LaporanBarangDetail);
        }
      })
      .then(() => {
        this.setState({ isLoading: false });
      })
      .catch((error) => this.errorHandling(error));
  }
  errorHandling(err) {
    NotifError(err);
    this.setState({ isLoading: false });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan Timbang Baki</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Timbang Baki Detail</li>
        </ol>
        <h1 className="page-header">Laporan Timbang Baki</h1>
        <Panel>
          <PanelHeader>Filter Laporan</PanelHeader>
          <br />
          <div className="container">
            <div className="row">
              <div className="col-12">
                <HeadFilterLaporanTimbangBarang
                  kode_baki={this.state.kode_baki}
                  export={this.state.export}
                  columns={this.state.columns}
                  data={this.state.LaporanBarangDetail}
                  isLoading={this.state.isLoading}
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
        
            </div>
          </div>
          <br />
        </Panel>
      </div>
    );
  }
}

FormLaporanTimbangBarang = reduxForm({
  form: "FormLaporanTimbangBarang",
  enableReinitialize: true,
})(FormLaporanTimbangBarang);
export default connect(maptostate, null)(FormLaporanTimbangBarang);
